let arr = []; //
let arrFontSize = []; //

//
const getAllTags = (obj) => {
  for (let i = 0; i < obj.length; i++) {
    if (obj[i].children) { //,
      getAllTags(obj[i].children);
    }
    //fontSize 
    if (!!obj[i].style.fontSize) {
      arr.push(obj[i]); //arr
      arrFontSize.push(parseInt(obj[i].style.fontSize.substring(0, obj[i].style.fontSize.length - 2))); //
    }
  }
}

//   
const setFontSize = (base) => {
  arr.map((item, index) => {
    if (!!item.style.fontSize) {
      item.style.fontSize = arrFontSize[index] * base + 'em';
      item.style.lineHeight = arrFontSize[index] * base * 1.7 + 'em'; // 
    }
  });
}

// px => rem
export const pxTorem = (content) => {
  content = content.replace(/(\d+)px/g, function(s, t) {
    s = s.replace('px', '');
    var value = parseInt(s) * 1/37.5; // 100px = 1rem
    return value + "rem";
  });
  return content
}

/**
 * fs  enum ['small','normal','heavy','super']
 */
export const setContentFontSize = (fs) => {
  // console.log(store,"store fontsize")
  switch (fs) {
    case "small":
      return "0.8em"
    case "heavy":
      return "1.1em"
    case "super":
      return "1.2em"
    default: //"normal":
      return "1em"
  }
}



